@charset "UTF-8";
.navLink {
  font-family: "Rubik"; }

.navLink:before {
  content: "";
  display: block;
  border-top: 2px solid #F18875;
  width: 110px;
  padding-bottom: 5px; }

.navLink:last-child:after {
  content: "";
  display: block;
  border-bottom: 2px solid #F18875;
  width: 110px;
  padding-top: 5px; }

.navLink:hover a::before {
  display: inline-block;
  content: "•";
  margin-right: 2px; }

.navLink:hover a::after {
  display: inline-block;
  content: "•";
  margin-left: 2px; }
